import React from 'react'
import Link from 'gatsby-link'

const Header = ({ siteTitle }) => (
  <nav className="navbar">
      <Link to="/" className="navbar-brand"><i className="logo fa fa-code"></i>Kacper Cieciński_</Link>
        <div>
        <ul className="navbar-nav">
              <li className="nav-item">
                  <Link className="nav-link" to="about">About<span className="sr-only">(current)</span></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="projects">Projects<span className="sr-only">(current)</span></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="contact">Contact<span className="sr-only">(current)</span></Link>
              </li>
          </ul>
        </div>

      
  </nav>
)

export default Header



